.header {
  max-width: calc(100% - 140px);
  height: 84px;
  padding: 0 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Open Sans", "Inter", Arial, sans-serif;
  color: #323232;
}

.header__links {
  display: flex;
  align-items: center;
  padding: 0;
  list-style-type: none;
}

.header__link {
  padding-right: 40px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;  
  text-align: center;  
  color: #323232;
  margin: 0;
  cursor: pointer;
  text-decoration: none;
}

.header__link:hover {
  opacity: 0.8;
}

.header__link:last-child {
  padding-left: 0;
}

.header__link_logout {
  margin-right: 30px;
}

.header__link_login {
  margin-right: 16px;
}

.header__link_active {
  font-weight: 500;
}

.header__button {
  background-color: #a1b29f;
  width: 76px;
  height: 32px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  border: none;
  border-radius: 0;
  padding: 0;
  cursor: pointer;
  color: #f6f6f6;
}

.header__burger-button {
  width: 44px;
  height: 44px;
  padding: 0;
  border: none;
  background: transparent url("../../images/img_burger.svg") no-repeat center center;
  cursor: pointer;
}

.header__burger-button:hover {
  opacity: 0.8;
}

.header__profile-button {
  width: 150px;
  height: 60px;
  border: 1px solid #aaaaaa;
  border-radius: 0;
  background: #a1b29f;
  cursor: pointer;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  padding: 0;
  text-decoration: none;
}

.header__profile-button:hover {
  opacity: 0.7;
  border: none;
}
.header__profile-img {
  padding-right: 7px;
  text-decoration: none;
  color: #f6f6f6;
}
.header__logo {
  width: 170px;
  height: 50px;
}

.header__logo:hover {
  cursor: pointer;
  opacity: 0.8;
}

@media screen and (max-width: 1023px) {
  .header {
    max-width: calc(100% - 60px);
    padding: 0 30px;
    margin: 0 auto;
  }
}

@media screen and (max-width: 767px) {
  .header {
    max-width: calc(100% - 28px);
  }

  .header__button {
    width: 54px;
    height: 26px;
    font-weight: 500;
    font-size: 10px;
  }

  .header__link {
    font-size: 10px;
  }

  .header__link_logout {
    margin-right: 14px;
  }
}
