.promo {
  max-width: calc(100% - 140px);
  max-height: 719px;
  margin: 0px auto 0;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  font-family: "Tenor Sans", "Inter", Arial, sans-serif;
  color: #323232;
}
.promo__conteiner {
  max-width: 100%;
  max-height: 719px; 
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
}

.promo__img-main {
  border: 1px solid purple;
  max-width: 100%;
  max-height: 719px;
  display: grid;
  object-fit: cover;
  object-position: center;
}
.promo__img {
  border: 1px solid red;
  max-width: 100%;
  display: grid;
  object-fit: cover;
  object-position: center;
}

.promo__description {
  border: 1px solid yellow;
  max-width: 100%;
  max-height: 719px;
  display: flex;
  flex-direction: column;
  margin: 0;
}

.promo__text {
  font-family: "Open Sans", "Inter", Arial, sans-serif;
  color: #323232;
  /* width: 562px; */
  display: flex;
  margin-left: 0;
  padding: 50px 122px 0 40px;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
}

.promo__title {
  font-family: "Tenor Sans", "Inter", Arial, sans-serif;
  color: #323232;
  /* width: 562px; */
  display: flex;
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  line-height: 120%;
  padding: 0px 122px 0 40px;
}

.promo__subtitle {
  display: flex;
  margin-left: 0;
  padding: 0px 122px 0 40px;
  /* width: 562px; */
  /* height: 27px; */
  font-family: "Open Sans", "Inter", Arial, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 150%;
  color: #323232;
}

.promo__profile-button {
  display: flex;
  width: 245px;
  height: 65px;
  background: #a1b29f;
  color: #f6f6f6;
  text-decoration: none;
  margin-left: 40px;
  margin-top: 20px;
  border: none;
  cursor: pointer;
}
.promo__btn {
  display: flex;
  width: 245px;
  height: 65px;
  justify-content: center;
  place-items: center;
  text-align: center;
  font-family: "Open Sans", "Inter", Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #f6f6f6;
  text-decoration: none;
}

.promo__btn:hover {
  opacity: 0.8;
}

.promo__social {
  display: flex;
  /* width: 562px; */
  justify-content: flex-end;
  margin-right: 140px;
  margin-top: 30px;
  padding-right: 0;
}

.promo__social-item {
  display: flex;
}

.promo__social-icon {
  width: 30px;
  height: 30px;
  display: flex;
}

.promo__social-icon:last-child {
  padding-left: 40px;
}

.promo__social-icon:hover {
  opacity: 0.8;
}

@media screen and (max-width: 1023px) {
  .promo {
    max-width: calc(100% - 40px);
    min-height: 834px;
  }

  .promo__title {
    /* width: 680px; */
    text-align: center;
    margin: 350px 24px 0;
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 52px;
  }

  .promo__conteiner {
    margin: 314px auto 30px;
  }

  .promo__btn {
    width: 84px;
    height: 57px;
    font-size: 12px;
    line-height: 15px;
  }
  .promo__img {
    max-width: 80%;
    border-radius: 10px;
    object-fit: cover;
    object-position: center;
  }
}

@media screen and (max-width: 767px) {
  .promo {
    max-width: calc(100% - 28px);
    min-height: 586px;
    margin: 14px auto 0;
  }

  .promo__conteiner {
    padding-top: 60px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    align-items: center;
     row-gap: 40px
  }

  .promo__title {
    width: 292px;
    margin: 220px 0 0 0;
    font-size: 29px;
    line-height: 37px;
  }

  .promo__conteiner {
    margin: 212px auto 17px;
  }

  .promo__btn {
    width: 84px;
    height: 57px;
    font-size: 12px;
    line-height: 15px;
  }

  .promo__img {
    width: 270px;
    height: 327px;
    border-radius: 10px;
    object-fit: cover;
    object-position: center;
  }
}
