.about-me {
  max-width: calc(100% - 140px);
  padding: 0px 70px 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #E5E5E5;
  color: #323232;
}

.about-me__conteiner {
  padding-top: 0;
  display: flex;
  flex-direction: row;
  column-gap: 270px;
}

.about-me__profile {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.about-me__name {
  margin: 0;
  padding-bottom: 18px;
  font-family: "Tenor Sans", "Inter", Arial, sans-serif;
  color: #323232;
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  line-height: 120%;
}

.about-me__description {
  margin: 0;
  padding-bottom: 40px;

  font-family: "Open Sans", "Inter", Arial, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 150%;
  color: #323232;
}

.about-me__profile-button {
  display: flex;
  width: 245px;
  height: 65px;
  background: #a1b29f;
  color: #f6f6f6;
  text-decoration: none;
  margin-left: 10px;
  margin-top: 40px;
  border: none;
  cursor: pointer;
}
.about-me__btn {
  display: flex;
  width: 245px;
  height: 65px;
  justify-content: center;
  place-items: center;
  text-align: center;
  font-family: "Open Sans", "Inter", Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #f6f6f6;
  text-decoration: none;
}

.about-me__btn:hover {
  opacity: 0.8;
}

