.about-me__img {
  width: 719px;
  height: 774px;
  border-radius: 0;
  object-fit: cover;
  object-position: center;
}

@media screen and (max-width: 1023px) {
  .about-me {
    max-width: calc(100% - 100px);
    padding: 90px 50px 91px;
    margin: 0 auto;
  }

  .about-me__name {
    padding-bottom: 16px;
    font-size: 40px;
    line-height: 40px;
  }

  .about-me__description {
    padding-bottom: 20px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
  }

  .about-me__conteiner {
    column-gap: 49px;
  }

  .about-me__img {
    width: 255px;
    height: 307px;
  }
}

@media screen and (max-width: 767px) {
  .about-me {
    max-width: calc(100% - 28px);
    padding: 70px 14px 69px;
  }

  .about-me__name {
    padding-bottom: 20px;
    font-size: 30px;
    line-height: 36px;
  }

  .about-me__description {
    font-size: 11px;
    line-height: 16px;
  }

  .about-me__conteiner {
    padding-top: 60px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    align-items: center;
    row-gap: 40px;
  }

  .about-me__img {
    width: 292px;
    height: 352px;
  }
}
